import React, { useState, useEffect } from 'react'
import './Circle.css'

const xyOnCircle = (radius, angle, cx, cy) => {
  angle = angle * (Math.PI / 180)
  const x = cx + radius * Math.cos(angle)
  const y = cy + radius * Math.sin(angle)
  return { x, y };
}

const Circle = ({
  text = '',
  size,
  ringRadius,
  angle,
  link = null,
  picture = null,
  styles = {},
  spinDirection = -1,
  spin = null,
  center = null
}) => {
  let [ cAngle, setAngle ] = useState(angle)
  let [ hovering, setHover ] = useState(false)

  useEffect(() => {
    let interval

    if (spin)
      interval = setInterval(() => {
        if (!hovering)
          setAngle(cAngle + spinDirection)
      }, 20);

    return () => clearInterval(interval)
  })

  let style = {
    ...styles,
    width: `${size}px`,
    height: `${size}px`
  }

  if (!center) {
    let { x, y } = xyOnCircle(ringRadius, cAngle, ringRadius, ringRadius)
    x -= (size / 2)
    y -= (size / 2)

    style.top = `${x}px`
    style.left = `${y}px`
  }

  if (hovering) {
    style.borderColor = '#0074D9'
    style.boxShadow = '0px 0px 20px 0px hsla(208, 100%, 85%, 1.0)'
  }

  if (picture) {
    style.background = `url("${picture}") center center / contain no-repeat`
  }

  return (
    <a
      href={link}
      className="circle"
      style={style}
      onMouseEnter={() => setHover(true)}
      onMouseLeave={() => setHover(false)}
      target="_blank"
      rel="noopener noreferrer"
    >
      {text}
    </a>
  )
}

export default Circle
