import React from 'react'
import './Ring.css'

const noop = () => {}

const Ring = ({
  size,
  title = null,
  color = '#6F7278',
  onMouseEnter = noop,
  onMouseLeave = noop,
  children
}) => {
  let style = {
    width: `${size}px`,
    height: `${size}px`,
    borderColor: color
  }

  return (
    <div
      className="ring"
      style={style}
      onMouseEnter={onMouseEnter}
      onMouseLeave={onMouseLeave}
    >
      {title && <h4 className="ring-title">{title}</h4>}
      {children}
    </div>
  )
}

export default Ring
